
import { useState } from '@wordpress/element';
import { __ } from '@wordpress/i18n';

import { PanelBody, PanelRow, SelectControl, RangeControl, Button, Dashicon, __experimentalUnitControl as UnitControl, TextControl, ToggleControl } from '@wordpress/components';

import { HelpPanel, IconControl, Label, ItemsPanel } from '../../../../bpl-tools/Components';
import { BDevice } from '../../../../bpl-tools/Components/Deprecated';

import { BControlPro, SelectControlPro } from '../../../../bpl-tools/ProControls';

import { placeholderImg } from '../../utils/links';
import { layoutOpt, lbType, pxUnit, perUnit, emUnit, ratioOpt, sliderOpt } from '../../utils/options';
import Item from './Item';

const General = ({ premiumProps, attributes, setAttributes, isBtnType, updateAttr }) => {

    const [device, setDevice] = useState('desktop');
    const [sliderDevice, setSliderDevice] = useState('desktop');

    const { lightboxType, items, layout, image, columns, columnGap, rowGap, content, galleryIcon, button, sliderHeight, slider } = attributes

    const itemsProps = { attributes, setAttributes, arrKey: 'items', premiumProps }

    return <>
        <HelpPanel slug="lightbox-block" docsLink="https://bplugins.com/docs/lightbox-block" />

        {/* Select Lightbox Type  */}
        <PanelBody className='bPlPanelBody' title={__('Lightbox', 'lightbox')} initialOpen={false}>
            <SelectControl labelPosition="side" label={__('Type:', 'lightbox')} value={lightboxType} options={lbType} onChange={val => { setAttributes({ lightboxType: val }) }} />

            {/* Check button and 1 gether than  */}
            {'button' == lightboxType && items.length > 1 && <>
                <Button className='removeItem removeAllitem' onClick={() => setAttributes({ items: [items[0]] })}> <Dashicon icon='trash' size={18} />{__('Delete Gallery Data', 'lightbox')}</Button>
            </>}
        </PanelBody>


        <PanelBody className='bPlPanelBody' title={isBtnType ? 'Lightbox Item' : 'Lightbox Items'}>
            {isBtnType ?
                <Item {...itemsProps} index={0} /> :
                <ItemsPanel {...itemsProps} newItem={{
                    type: "image",
                    thumbnail: placeholderImg,
                    thumbCaption: "",
                    content: placeholderImg,
                    contentCaption: "",
                    caption: "Caption Here",
                    importCaption: false,
                    altText: null
                }} ItemSettings={Item} itemLabel='Item' design={premiumProps?.isPremium ? 'sortable' : 'all'} />
            }
        </PanelBody>

        {/* lightbox type button than Hide Layout Panel */}
        {
            !isBtnType &&
            <PanelBody title={__('Layout', 'lightbox')} className="bPlPanelBody" initialOpen={false} >
                <ToggleControl label={__('Showing the icon', 'lightbox')} checked={galleryIcon} onChange={(val) => { setAttributes({ galleryIcon: val }); }} />

                <SelectControlPro label={__('Layout', 'lightbox')} className='mt10' labelPosition="side" value={layout} options={layoutOpt} onChange={(val) => {
                    setAttributes({ layout: val })
                }} __nextHasNoMarginBottom proValues={['slider']}  {...premiumProps} />


                {/* column define option  */}
                <PanelRow className='mt20'>
                    <Label mt='0'>{__('Columns:', 'lightbox')}</Label>
                    <BDevice device={device} onChange={val => setDevice(val)} />
                </PanelRow>
                <RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' />

                {/* column Gap  */}
                <UnitControl className='mt20' label={__('Column Gap:', 'lightbox')} labelPosition='left' value={columnGap} onChange={val => setAttributes({ columnGap: val })} units={[pxUnit(30), perUnit(3), emUnit(2)]} isResetValueOnUnitChange={true} />

                {/* row Gap  */}
                <UnitControl className='mt20' label={__('Row Gap:', 'lightbox')} labelPosition='left' value={rowGap} onChange={val => setAttributes({ rowGap: val })} units={[pxUnit(40), perUnit(3), emUnit(2.5)]} isResetValueOnUnitChange={true} />
            </PanelBody>
        }

        {
            layout !== "masonry" && <PanelBody className='bPlPanelBody' title={__('Image Aspect Ratio', 'bp-g-photos')} initialOpen={false}>
                <BControlPro className="mt15" label={__('Ratio', 'bp-g-photos')} labelPosition="side" value={image?.ratio} options={ratioOpt} onChange={(val) => {
                    setAttributes({ image: { ...image, ratio: val } })
                }} Component={SelectControl} {...premiumProps} />
            </PanelBody>
        }

        {/* lightbox type button than show button settings  */}
        {
            isBtnType && <PanelBody title={__('Button', 'lightbox')} className="bPlPanelBody" initialOpen={false} >
                <Label className='mb5'>{__('Text', 'lightbox')} </Label>
                <TextControl value={button.text} labelPosition='top' onChange={val => updateAttr('button', 'text', val)} />

                <IconControl className='mt20' value={button.icon} onChange={val => updateAttr('button', 'icon', val)} isSize={false} isColor={false} />
            </PanelBody>
        }

        {
            lightboxType === 'gallery' && <PanelBody title={__("Gallery", "lightbox")} className="bPlPanelBody" initialOpen={false}>
                <BControlPro className='mt10' label={__('Show Caption', 'lightbox')} checked={content?.caption} onChange={val => { updateAttr('content', 'caption', val); }} Component={ToggleControl}  {...premiumProps} />

                <BControlPro className='mt10' label={__('Show Overlay', 'lightbox')} checked={content?.overlyColor} onChange={val => { updateAttr('content', 'overlyColor', val); }} Component={ToggleControl} {...premiumProps} />
            </PanelBody>
        }

        {
            layout === 'slider' && <PanelBody title={__('Slider')} className="bPlPanelBody" initialOpen={false}>
                <PanelRow className='m0'>
                    <Label mt='0'>{__('Height:', 'lightbox')}</Label>
                    <BControlPro device={sliderDevice} onChange={val => setSliderDevice(val)} Component={BDevice} {...premiumProps} />
                </PanelRow>

                <BControlPro value={sliderHeight[sliderDevice]} onChange={val => { setAttributes({ sliderHeight: { ...sliderHeight, [sliderDevice]: val } }) }} units={[pxUnit(550), perUnit(90)]} isResetValueOnUnitChange={true} Component={UnitControl} {...premiumProps} />

                <BControlPro labelPosition="side" label={__('Thumbnails:', 'lightbox')} value={slider?.thumbs} options={sliderOpt} onChange={val => { updateAttr('slider', 'thumbs', val) }} Component={SelectControl} {...premiumProps} />
            </PanelBody>
        }
    </>
}
export default General;